html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-y: scroll;
}
html,
body {
  overflow-x: hidden;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
ul,
p {
  margin: 0;
  padding: 0;
}
ul,
a {
  list-style: none;
  text-decoration: none;
  padding-left: 0;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.75;
  letter-spacing: 0.04em;
  margin: 0;
  color: var(--dark-text-color);
  width: 100vw;
}

.container {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: 1200px) {
    max-width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    max-width: 768px;
    padding-left: 33px;
    padding-right: 33px;
  }
  @media screen and (min-width: 320px) and (max-width: 767px) {
    max-width: 320px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
.backdrop,
.read-backdrop,
.locations-backdrop,
.franchise-backdrop {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.4s, visibility 0.4s;
  background-color: rgba(86, 107, 98, 0.4);
}

.modal-open {
  overflow: hidden;
}
.modal-open .backdrop,
.modal-open .read-backdrop,
.modal-open .locations-backdrop,
.modal-open .franchise-backdrop {
  opacity: 0;
  visibility: hidden;
}
.modal-open .modal,
.modal-open .read-modal,
.modal-open .locations-modal,
.modal-open .franchise-modal {
  transform: translate(-50%, -50%) scale(0);
  opacity: 0;
  visibility: hidden;
}
.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop.is-visible,
.read-backdrop.is-visible,
.locations-backdrop.is-visible,
.franchise-backdrop.is-visible {
  opacity: 1;
  visibility: visible;
}
.modal.is-visible,
.read-modal.is-visible,
.locations-modal.is-visible,
.franchise-modal.is-visible {
  transform: translate(-50%, -50%) scale(1);
  opacity: 1;
  visibility: visible;
}

.modal,
.read-modal,
.locations-modal,
.franchise-modal {
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--light-text-color);
  width: 568px;
  border: 2px solid var(--accent-color);
  border-radius: 10px;
  top: 50%;
  left: 50%;

  opacity: 0;
  visibility: hidden;
  transform: translate(-50%, -50%) scale(0);
  transition: transform 0.8s, opacity 0.8s, visibility 0.8s;

  @media screen and(min-width: 320px) and (max-width: 767px) {
    width: 280px;
    height: 100vh;
  }
}

.modal-close-btn,
.read-more-close-btn,
.locations-close-btn,
.franchise-close-btn {
  border-width: 0;
  margin-left: auto;
  background-color: var(--light-text-color);
  margin-top: 20px;
  margin-right: 20px;
  cursor: pointer;
  @media screen and (max-width: 767px) {
    margin-top: 8px;
    margin-right: 8px;
    padding: 0;
  }

  &:hover {
    fill: var(--hover-btn);
    transform: scale(1.2);
  }
}
.modal-title {
  font-size: 20px;
  text-align: center;
  font-family: 'Seymour One';
}
.title {
  font-family: 'Seymour One';
  font-size: 20px;
  margin-bottom: 32px;
  text-align: center;
  @media screen and (max-width: 767px) {
    font-size: 16px;
    margin-bottom: 20px;
  }
}
