:root {
  --main-background: #84a178;
  --accent-color: #f8d24e;
  --dark-text-color: #131313;
  --light-text-color: #fcfcfc;
  --text-accent-color: #d6936d;
  --card-accent: #efd478;
  --hover-btn: #0a8817;
  --disabled: #ababab;
  --rights-reserved: #7a7a7a;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-y: scroll;
}

html, body {
  overflow-x: hidden;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, ul, p {
  margin: 0;
  padding: 0;
}

ul, a {
  padding-left: 0;
  text-decoration: none;
  list-style: none;
}

body {
  letter-spacing: .04em;
  color: var(--dark-text-color);
  width: 100vw;
  margin: 0;
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.75;
}

.container {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .container {
    max-width: 768px;
    padding-left: 33px;
    padding-right: 33px;
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .container {
    max-width: 320px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

.backdrop, .read-backdrop, .locations-backdrop, .franchise-backdrop {
  z-index: 10;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(86, 107, 98, .4);
  transition: opacity .4s, visibility .4s;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-open {
  overflow: hidden;
}

.modal-open .backdrop, .modal-open .read-backdrop, .modal-open .locations-backdrop, .modal-open .franchise-backdrop {
  opacity: 0;
  visibility: hidden;
}

.modal-open .modal, .modal-open .read-modal, .modal-open .locations-modal, .modal-open .franchise-modal {
  opacity: 0;
  visibility: hidden;
  transform: translate(-50%, -50%)scale(0);
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop.is-visible, .read-backdrop.is-visible, .locations-backdrop.is-visible, .franchise-backdrop.is-visible {
  opacity: 1;
  visibility: visible;
}

.modal.is-visible, .read-modal.is-visible, .locations-modal.is-visible, .franchise-modal.is-visible {
  opacity: 1;
  visibility: visible;
  transform: translate(-50%, -50%)scale(1);
}

.modal, .read-modal, .locations-modal, .franchise-modal {
  background-color: var(--light-text-color);
  width: 568px;
  border: 2px solid var(--accent-color);
  opacity: 0;
  visibility: hidden;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  transition: transform .8s, opacity .8s, visibility .8s;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(0);
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .modal, .read-modal, .locations-modal, .franchise-modal {
    width: 280px;
    height: 100vh;
  }
}

.modal-close-btn, .read-more-close-btn, .locations-close-btn, .franchise-close-btn {
  background-color: var(--light-text-color);
  cursor: pointer;
  border-width: 0;
  margin-top: 20px;
  margin-left: auto;
  margin-right: 20px;
}

@media screen and (max-width: 767px) {
