    margin-right: 8px;
    padding: 0;
  }
}

.modal-close-btn:hover, .read-more-close-btn:hover, .locations-close-btn:hover, .franchise-close-btn:hover {
  fill: var(--hover-btn);
  transform: scale(1.2);
}

